<template><section><h1>modal 组件</h1>
<blockquote>
<p>基础用法</p>
</blockquote>
<demo-block>
                  <div class="source" slot="source"><template>
    <div>
        <div class="inline-block">
            <button-component @click.native="selectModal">
                模态框默认
            </button-component>
            <modal :show-modal="showModal" :on-cancelfn="onCancelfn" :on-okfn="onOk">
            
                我是内容部分, 确认后1秒后关闭
            </modal>
        </div>
        <div class="inline-block">
            <button-component @click.native="selectModal2">
                模态框自定义
            </button-component>
            <modal :show-modal="showModal2" :on-cancelfn="onCancelfn2" :on-okfn="onOk2" :footer="true" titles="我是标题" content="我是内容部分, 确认后1秒后关闭">
            </modal>
        </div>

    </div>
</template>



</div>
                  <h3>通过 <code>transition</code> 方式实现动画</h3>

