<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: {
        ButtonComponent, Modal
    },
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">showModal</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">showModal2</span>: <span class="hljs-literal">false</span>,
      }
    },
    <span class="hljs-attr">methods</span>: {
		selectModal() {
			<span class="hljs-keyword">this</span>.showModal = !<span class="hljs-keyword">this</span>.showModal
		},
		selectModal2() {
			<span class="hljs-keyword">this</span>.showModal2 = !<span class="hljs-keyword">this</span>.showModal2
		},
		onOk() { <span class="hljs-comment">// modal 确定</span>
