</div></demo-block>
<blockquote>
<h2>开关配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:show-modal</td>
<td style="text-align:left">模态框是否显现</td>
</tr>
<tr>
<td>:on-cancelfn</td>
<td style="text-align:left">取消事件</td>
</tr>
<tr>
<td>:on-okfn</td>
<td style="text-align:left">确认事件</td>
</tr>
<tr>
<td>:footer</td>
<td style="text-align:left">底部确认和取消是否显现，默认显现</td>
</tr>
<tr>
<td>titles</td>
<td style="text-align:left">title 是否显现，不显现传false, 或传递要显现的内容</td>
</tr>
<tr>
<td>content</td>
<td style="text-align:left">内容部分</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="scss" scoped>
.inline-block{
    display: inline-block;
}
</style>
<script>
import { ButtonComponent, Modal } from 'vo-ui'
export default {
    components: {
        ButtonComponent, Modal
    },
    data() {
      return {
        showModal: false,
        showModal2: false,
      }
    },
    methods: {
		selectModal() {
			this.showModal = !this.showModal
		},
		selectModal2() {
			this.showModal2 = !this.showModal2
		},
		onOk() { // modal 确定
			setTimeout(() => {
				this.showModal = false
			}, 1000)
		},
		onOk2() { // modal 确定
			setTimeout(() => {
				this.showModal2 = false
			}, 1000)
		},
		onCancelfn() { // 关闭 || 取消modal 组件
			this.showModal = false
		},
		onCancelfn2() { // 关闭 || 取消modal 组件
			this.showModal2 = false
		}

    }
}
</script>


